.brands {
  margin: 180px 0;

  &__title {
    font-weight: 600;
    font-size: 29px;
    line-height: 116%;
    color: #003154;
    margin-bottom: 40px;
  }

  &__slider-container {
  }

  &__card {
    padding: 30px;
    border-left: 1px solid #E1E1E1;

    min-height: auto;
    color: #111;
    font-size: 16px;
    width: 20%;
    margin-right: 2.5%;
    margin-bottom: 2.5%;

    
  }
  &__card-item {
    padding: 0 16px;
  }

  &__img {
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 158%;
    color: #404040;
    max-width: 242px;
  }

  &__pagination {
  }
}

.card {
}

.slider-container {
  max-width: 1200px;
  padding: 0 15px;
  position: relative;
  margin: 0 auto;
}

/* swiper-bullets styling */
.swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  margin: 0 15px;
  background-color: tomato;
  opacity: 1;
}

.swiper-pagination-bullet-active {
  background-color: navy;
}

.swiper-container {
  padding-bottom: 50px;
  overflow: hidden;
  width: 100%;
}

.slider-container .swiper-wrapper {
  justify-content: space-between;
  flex-wrap: wrap;
}

@media (max-width: 600px) {
  .card {
    width: 100%;
    margin: 0;
    flex-shrink: 0;
  }

  .slider-container .swiper-wrapper {
    justify-content: initial;
    flex-wrap: nowrap;
  }
}
